export default function createTemplate({
  abilities,
  sprites,
  name,
  weight,
  height,
}) {
  const abilitiesMarkup = abilities
    .map(ability => `<li>${ability.ability.name}</li>`)
    .join('');

  return `
    <div class="card">
  <div class="card-img-top">
    <img src="${sprites.front_default}" alt="${name}">
  </div>
  <div class="card-body">
    <h2 class="card-title">–ò–º—è: ${name}</h2>
    <p class="card-text">–í–µ—Å: ${weight}</p>
    <p class="card-text">–†–æ—Å—Ç: ${height}</p>

    <p class="card-text"><b>–£–º–µ–Ω–∏—è</b></p>
    <ul>${abilitiesMarkup}</ul>
   
  </div>
</div>
    
    `;
}
